.dialogs{
    background-color: #fff;
    padding: 30px 50px;
}
.dialog img{
    width: 80px;
}
.dialog {
    padding: 20px ;
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-gap: 20px;
    background-color: #C0B9B0;
}
.active{
    color: gold;
}
.message{
    margin: 0;
    font-style: italic;
    font-size: 14px;
}
.dialogName{
    font-weight: bold;
    text-decoration: none;
    color: #000;
}